@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap);
.dark-mode-btn {
  order: 2;
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 51px;
  height: 26px;
  padding: 5px;
  border-radius: 50px;
  background-color: #272727;
}
.dark-mode-btn::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 1px;
  display: block;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #fff;
  transition: left 0.2s ease-in;
}
.dark-mode-btn--active::before {
  left: 26px;
}
.dark-mode-btn__icon {
  position: relative;
  z-index: 9;
}
/* Reset and base styles  */
* {
	padding: 0px;
	margin: 0px;
	border: none;
	box-sizing: border-box;
}

a, a:link, a:visited  {
    text-decoration: none;
}

ul, ul li {
	list-style: none;
}

:root {
	/* Colors */
	--black: #171718;
	--white: #fff;
	--purple: #407ab1;

	/* background */
	--black-bg: url(/my_portfolio/static/media/background-dark.efcc8de3.webp);
	--white-bg: url(/my_portfolio/static/media/background-white.d01ee31a.webp);

	/* Light mode */
	--page-bg: var(--white-bg);
	--text-color: var(--black);
	--title-1: var(--purple);
	--svg-fill: var(--black);
	--project-card-bg: var(--white);
	--project-card-text: var(--black);
	--box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
}

.dark {
	/* Dark mode */
	--page-bg: var(--black-bg);
	--text-color: var(--white);

	--title-1: var(--white);
	--svg-fill: var(--white);
	--project-card-bg: var(--black);
	--project-card-text: var(--white);
	--box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.8);
}

html,
body {
	width: 100%;
	height: 100vh;
	font-family: 'DM Sans', sans-serif;
	letter-spacing: -0.5px;
	display: flex;
	justify-content: center;

	/* Light mode */
	color: var(--text-color);
	overflow: hidden;
}

#root {
	max-width: 140vh;
	height: 100vh;
	display: flex;
	flex-direction: column;
}

.App {
	height: 89%;
	display: flex;
	flex-direction: column;
	background-image: var(--page-bg);
	background-size: cover;
	background-position: center;
}

.container {
	position: relative;
	margin: 0 auto;
	padding: 0 15px;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.container_scroll {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1vh;
    font-size: larger;
    flex: 1 1;
    overflow-y: auto;
	scrollbar-width: none;
}

.none {
	display: none !important;
}

.title-1 {
	margin-bottom: 2vh;
	font-size: 5vh;
	font-weight: 700;
	color: var(--title-1);
	text-align: center;
}

.title-2 {
	margin-top: 1vh;
	font-size: 4vh;
	font-weight: 700;
	text-align: center
}

svg {
	fill: var(--svg-fill);
}

.box-button {
	position: absolute;
	width: 60%;
	padding-left: 20px;
	padding-top: 2vh;
}

.nav {
  padding: 20px 20px 20px 100px;
  background-color: var(--black);
  border-bottom: 1px solid var(--purple);
  color: var(--white);
  letter-spacing: normal;
  z-index: 1;
}

.nav-row {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  -moz-column-gap: 30px;
  grid-column-gap: 30px;
  column-gap: 30px;
  grid-row-gap: 20px;
  row-gap: 20px;
  flex-wrap: wrap;
}

.logo {
  margin-right: auto;
  color: var(--white);
  font-size: 24px;
}

.logo strong {
  font-weight: 700;
}

.nav-list {
  display: flex;
  flex-wrap: wrap;
  grid-row-gap: 10px;
  row-gap: 10px;
  align-items: center;
  -moz-column-gap: 40px;
  grid-column-gap: 40px;
  column-gap: 40px;
  font-size: 16px;
  font-weight: 500;
}

.nav-list__link {
  color: var(--white);
  transition: opacity 0.2s ease-in;
}

.nav-list__link:hover {
  opacity: 0.8;
  color: var(--purple);
}

.nav-list__link--active {
  position: relative;
}

.nav-list__link--active::before {
  content: "";
  position: absolute;
  left: 0;
  top: 100%;
  display: block;
  height: 2px;
  width: 100%;
  background-color: var(--purple);
}

@media (max-width: 748px) {
  .nav {
    padding: 20px 20px 20px 20px;
  }
}

@media (max-width: 632px) {
  .nav-row {
    justify-content: space-between;
    grid-row-gap: 4px;
    row-gap: 4px;
  }

  .dark-mode-btn {
    order: 0;
  }

  .nav-list {
    font-size: 14px;
  }
}

@media (max-width: 384px) {
  .logo {
    font-size: 18px;
  }
  
  .nav-list {
    font-size: 12px;
    grid-column-gap: 20px;
    column-gap: 20px;
  }
}
@keyframes zoomIn {
  from {
    transform: scale(0);
    opacity: 0;
  }

  to {
    transform: scale(1);
    opacity: 1;
  }
}

.content-list {
	display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  grid-row-gap: 1vh;
  row-gap: 1vh;
}

.content-list a {
	color: var(--accent);
}

.content-list__item {
	display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: auto;
  font-size: 2vh;
  padding-bottom: 2vh;
  grid-row-gap: 2vh;
  row-gap: 2vh;
}

.content-list__item p+p {
	margin-top: 0.5em;
}

.skill {
  max-width: 8vh;
  max-height: 12vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: zoomIn 2s ease forwards;
}

svg {
  width: 8vw;
}

@media (max-width: 620px) {
	.content-list {
		grid-row-gap: 10px;
		row-gap: 10px;
	}

	.content-list__item {
		font-size: 12px;
    display: grid;
    grid: 1fr 1fr / 1fr 1fr 1fr 1fr;
	}
}

.header {
  padding: 20px 0 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  
}

.header__wrapper {
  height: auto;
  padding: 0 15px;

}

.header__title {
  margin-bottom: 2vh;
  font-size: 5vh;
  font-weight: 700;
}

.header__title strong {
  font-size: 6vh;
  font-weight: 700;
}

.header__title em {
  font-style: normal;
  color: var(--purple);
}

@media (max-width: 620px) {
	.header {
		min-height: unset;
	}

	.header__title {
		font-size: 3vh;
	}

	.header__title strong {
		font-size: 4vh;
	}
}

@media (max-width: 410px) {
	.header__title strong {
		font-size: 3vh;
	}
}


.project {
  max-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--white);
  box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  border: solid 1px var(--purple);
}

.project__img {
  max-width: 70vw;
	max-height: 50vh;
  border-radius: 10px;
  border: solid 1px var(--purple);
}

.project__title {
  padding: 15px 20px 25px;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.3;
  color: var(--black);
}

.projects-navigation {
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    padding: 0;
    margin: 20px 0;
    list-style-type: none;
}

.projects li {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: calc(33.33% - 10px);
    margin: 5px;
}

.slide__svg {
    width: 40px;
    height: 40px;
    fill: var(--text-color);
    transition: all 0.3s;
}


.slide__svg:hover {
    fill: var(--purple);
}


@media (max-width: 1026px) {
    .projects li {
        flex-grow: 1;
        flex-shrink: 1;
        flex-basis: calc(50% - 10px);
    }
}

@media (max-width: 816px) {
    .projects li {
        flex: 1 1 100%;
    }
}

@media (max-width: 620px) {
    .projects-navigation {
        width: 100%;
    }

    .slide__svg {
        width: 30px;
        height: 30px;
    }
}

.header, .projects-page, .contacts {
    position: absolute;
    top: 0;
    width: 100%;
    transition: 1.5s ease;
}

.active {
    opacity: 1;
    transform: translateY(0);
}

.up {
    opacity: 0;
    transform: translateY(-100%);
}

.down {
    opacity: 0;
    transform: translateY(100%);
}

.git-hub-button {
    display: flex;
    flex-direction: row;
    grid-gap: 10px;
    gap: 10px;
    padding: 10px 20px;
    margin-bottom: 30px;
    background-color: var(--purple);
    color: var(--white); 
    border: none; 
    border-radius: 5px;
    text-decoration: none;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.git-hub-button:hover {
    background-color: #0056b3;
}

.git-hub-button:active {
    background-color: #004080;
}
.back-button {
    display: inline-block;
    padding: 10px 20px;
    background-color: var(--purple);
    color: var(--white); 
    border: none; 
    border-radius: 5px;
    text-decoration: none;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.back-button:hover {
    background-color: #0056b3;
}

.back-button:active {
    background-color: #004080;
}

@media (max-width: 620px) {
    .back-button {
        visibility: hidden;
    }
}
.project-details {
	margin: 0 auto;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
}

.project-details__img {
	max-width: 70vw;
	max-height: 50vh;
	margin-bottom: 40px;
	box-shadow: var(--box-shadow);
	border-radius: 10px;
}

.project-details__desc {
	margin-bottom: 30px;
	font-weight: 700;
	font-size: 24px;
	line-height: 1.3;
}

.header_about {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.photo {
    width: 200px;
    height: 266px;
    margin: 1vw;
}

@media (max-width: 620px) {
    .header_about {
        flex-direction: column;
    }
}
